import React from 'react'
import { graphql, navigate } from 'gatsby'
import { Page, Resume } from './../components'

const ResumePage = ({
  data: {
    site: { siteMetadata: metadata },
  },
}) => {
  return (
    <Page
      title="Resume"
      metadata={metadata}
      titleLinkHandler={() => navigate('/')}
    >
      <Resume metadata={metadata} />
    </Page>
  )
}

export default ResumePage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        description
        year
        author {
          name
          avatar
          bio
          social {
            phone
            twitter
            email
            github
            linkedin
          }
        }
        resume {
          skills {
            name
            level
          }
          timeline {
            name
            events {
              title
              subtitle
              type
              startDate(formatString: "MMMM YYYY")
              endDate(formatString: "MMMM YYYY")
              overview
              points
              link
            }
          }
        }
      }
    }
  }
`
